@import 'src/styles.module.scss';

.authorsContainer {
  width: 100%;
  margin-bottom: .5rem;
}

.list {
  width: 100%;
  margin: 1rem;
  @include flex;
  flex-wrap: wrap;
  justify-content: center;
  line-height: 2rem;

  &.multipleInstitutes > :not(:first-child) {
    border-left: 2px solid $grey;
  }
}

.author {
  @include flex;
  padding: 0 .5rem;
  font-size: 1.2rem;
  text-decoration-color: transparent;
  color: $honolulu_blue;
  text-align: center;
  white-space: nowrap;
  transition: all .1s ease;

  &:hover {
    text-decoration-color: $honolulu_blue;
  }
}

.institute{
  padding: 0 .5rem;
  font-size: 1.2rem;
}