@import '../../styles.module';

.container {
  @include flex;
  justify-content: flex-start;
  padding: .8rem 1.5rem;
  width: calc(100% - 3rem);
  height: calc($nav_bar_height - 1.6rem);
  box-shadow: 0px 0.1rem 0.3rem rgba(0, 0, 0, 0.3);
}

.title {
  font-size: 2rem;
  font-family: Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif;
  font-weight: bolder;
  color: black;
  margin-right: 1rem;
}

.logo {
  max-height: 3.2rem;
}

.pracsysLogo {
  margin-right: 1rem;
  padding-right: 1rem;
  border-right: 3px solid black;
}

.rutgersLogo {
  margin-left: auto;
}